<template>
  <div class="home">
    <!-- 顶部导航栏 -->
    <div class="navBar">
      <van-nav-bar title="首页" />
    </div>
    <!-- 轮播图 -->
    <div class="swiper">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="item in swiperData" :key="item.id">
          <!-- 注意点 要使用循环遍历的数据时 记得在属性前加上动态绑定 -->
          <img :src="item.url" alt="">
        </van-swipe-item>
      </van-swipe>
    </div>
    <!-- 菜单栏 产品分类相关接口 -->
    <div class="gridMenu">
      <van-grid :column-num="3">
        <van-grid-item v-for="item in categoryData" :key="item.id" @click="toProductDetailPage(item)" :icon="item.icon" :text="item.name" />
      </van-grid>
    </div>
    <!-- 产品展示列表 product-controller : order_产品相关接口 分页查询产品相关信息-->
    <div class="productList">
      <van-card
        price="2.00"
        desc="描述信息"
        title="商品标题"
        thumb="https://img01.yzcdn.cn/vant/ipad.jpeg"
      />
    </div>
  </div>
</template>

<script>
// 引入axios
import {get} from '../../http/axios'
export default {
  data() {
    return {
      // 轮播图的数组
      swiperData:[],
      // 分类数据
      categoryData:[]
    }
  },
  // 生命周期钩子函数 在页面加载的特定时期 自动的去执行一些方法 获取数据
  created() {
    // 需要自动执行获取数据的方法 放到这个生命周期里执行就可以了
    // 自动执行获取轮播图的数据
    this.getSwiperData()
    // 自动获取菜单栏的信息
    this.getProductCategoryData()
  },
  methods: {
    // 跳转到商品详情页面 并且 传递产品分类相对应的ID过去
    toProductDetailPage(category){
      // console.log(productCategoryId);
      // 跳转到详情页面 并 传参
      this.$router.push({
        // 页面的路径
        path:'/manage/productList',
        // 传递的参数
        query:{
          // 产品分类ID
          productCategoryId: category.id,
          // 分类的名称
          categoryName: category.name
        }
      })
    },
    // 发送网络请求 获取轮播图相关的数据
    async getSwiperData(){
      // 1.发送网络请求 获取对应的数据
      let swiperData = await get('/carousel/findAll')
      // console.log(swiperData.data.data);
      // 2.对请求成功返回的数据进行筛选 对data中的对应的变量进行赋值
      this.swiperData = swiperData.data.data
    },
    // 获取产品分类数据
    async getProductCategoryData(){
      // 设置参数
      let data = {
        page: 1,
        pageSize: 6
      }
      // 发送请求
      let categoryData = await get('/productCategory/pageQuery', data)
      // console.log(categoryData.data.data.list);
      this.categoryData = categoryData.data.data.list
    }
  },
};
</script>

<style scoped>
/* 设置轮播图里图片的样式 */
.my-swipe .van-swipe-item img{
  width: 100%;
  height: 240px;
}
</style>